/*   全局变量   start   */
/*   全局变量   end   */
/*   Mixins  start   */
/*   Mixins  end   */
html,
body {
  margin: 0;
  padding: 0;
  font: 14px 'microsoft YaHei';
  -webkit-font-smoothing: antialiased;
  background-color: #f5f5f5;
}
li,
ul {
  list-style: none;
}
p,
li,
ul,
dd,
dt,
dl,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  line-height: 1;
}
a,
dd,
div,
dl,
i,
img,
input,
li,
p,
span,
ul {
  box-sizing: border-box;
}
a,
a:hover,
a:link,
a:visited {
  border: 0 none;
  text-decoration: none;
}
/*    文本选中颜色  */
::selection {
  background: #35b558;
  color: #fff;
}
/*    公共容器      */
.wrap {
  width: 1000px;
  margin: 0 auto;
  min-width: 1000px;
}
/*   清除浮动   */
.clear:after {
  content: '';
  display: block;
  overflow: hidden;
  height: 0;
  font-size: 0;
  clear: both;
}
/*   小三角   */
.triangle:after {
  content: '';
  transform: rotate(45deg);
  display: none;
  position: absolute;
  width: 15px;
  height: 15px;
  border-top: 1px solid #ececec;
  border-left: 1px solid #ececec;
  background: #fff;
}
/*    周年活动   start   */
.ad-layer {
  z-index: 999;
  position: fixed;
  left: 50%;
  width: 98px;
  height: 200px;
  margin-left: 515px;
  margin-bottom: -100px;
  bottom: 50%;
  background: transparent;
  overflow: hidden;
}
.ad-layer a {
  position: relative;
  left: -12px;
}
.ad-layer .close {
  display: block;
  z-index: 999;
  position: absolute;
  top: 0;
  right: 0;
  width: 15px;
  height: 15px;
  background: #d3d3d3;
  cursor: pointer;
}
.ad-layer .close:before {
  content: '';
  display: inline-block;
  z-index: 999;
  position: absolute;
  top: 7px;
  left: 3px;
  width: 10px;
  height: 1px;
  background: #fff;
  transform: rotate(45deg);
}
.ad-layer .close:after {
  content: '';
  display: inline-block;
  z-index: 999;
  position: absolute;
  top: 7px;
  right: 2px;
  width: 10px;
  height: 1px;
  background: #fff;
  transform: rotate(-45deg);
}
.close-input {
  position: absolute;
}
.close-input:checked + .ad-layer {
  display: none;
}
/*    周年活动   end   */
/*   回到顶部  start   */
.go-top-wrap {
  position: fixed;
  left: 50%;
  margin-left: 510px;
  bottom: 50px;
  width: 34px;
  text-align: center;
  z-index: 2000;
}
.go-top-wrap .app-download-left {
  position: relative;
  display: block;
  background: #f7f7f7 url(../img/index/phone-1.png) center center no-repeat;
  background-size: 12px 24px;
  width: 34px;
  height: 35px;
  border-bottom: 1px solid #e0e0e0;
}
.go-top-wrap .app-download-left:hover .app-download-code {
  display: block;
}
.go-top-wrap .app-download-code {
  background: url(../img/index/appewm.png) center center no-repeat;
  background-size: 100%;
  width: 126px;
  height: 159px;
  position: absolute;
  z-index: 2100;
  left: -126px;
  top: -70px;
  display: none;
}
.go-top-wrap .go-top {
  display: block;
  background: url(../img/index/gotop.jpg) 0 0 no-repeat;
  background-size: 34px 103px;
  width: 34px;
  height: 34px;
}
.go-top-wrap .go-top:hover {
  background: url(../img/index/gotop2.jpg) 0 0 no-repeat;
  background-position: 0 0;
  background-size: 34px 103px;
}
/*   回到顶部  end   */
/*   导航动画   */
@keyframes list-show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
/*   搜索框动画   */
@keyframes scale-run {
  0% {
    opacity: 0;
    transform: scale(0.1, 1);
  }
  100% {
    opacity: 1;
    transform: scale(1, 1);
  }
}
/*     元素块      */
.section {
  margin-bottom: 40px;
  box-sizing: border-box;
  overflow: hidden;
}
/*     块标题     */
.section-title {
  font-size: 18px;
  color: #555;
  height: 18px;
  line-height: 18px;
  margin-bottom: 22px;
  font-weight: 400;
}
/*    渐变块   start  */
.shadow {
  transition: box-shadow 0.5s;
}
.shadow:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
  transform: translateZ(0);
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-perspective: 1000px;
  -moz-perspective: 1000px;
  perspective: 1000px;
}
/*    渐变块   end  */
/*    渐变按钮  start      */
